import { useState } from "react";

import AgesTable from "../AgesTable";

const AgeDemographic = () => {
  const options = [
    "carrot",
    "apple",
    "grapes",
    "cake",
    "tv",
    "crackers",
    "chips",
    "ham",
    "beef",
  ];
  const [ages, setAges] = useState({});

  const onChangeHandler = async (e) => {
    if (e.target.value === "Item") return setAges({});

    const response = await fetch(
      `http://localhost:3000/users/age/${e.target.value}`
    );
    const data = await response.json();

    setAges(data);
  };

  return (
    <>
      <h2>Age Demographic of Users with _____</h2>

      <div onChange={onChangeHandler}>
        <select className="btn btn-primary" name="Item">
          <option defaultValue>Item</option>
          {options.map((option) => (
            <option value={option} key={option}>
              {option}
            </option>
          ))}
        </select>
      </div>

      <AgesTable ages={ages} />
    </>
  );
};

export default AgeDemographic;
