const UsersTable = ({ users }) => (
  <table className="table table-striped">
    <thead>
      <tr>
        <th scope="col">Username</th>
        <th scope="col">Age</th>
      </tr>
    </thead>
    <tbody>
      {users.map((user, index) => (
        <tr key={index}>
          <td>{user.username}</td>
          <td>{user.age}</td>
        </tr>
      ))}
    </tbody>
  </table>
);

export default UsersTable;
