const AgesTable = ({ ages }) => (
  <table className="table table-striped">
    <thead>
      <tr>
        <th>Age</th>
        <th>Count</th>
      </tr>
    </thead>
    <tbody>
      {Object.entries(ages).map(([age, count], index) => (
        <tr key={index}>
          <td>{age}</td>
          <td>{count}</td>
        </tr>
      ))}
    </tbody>
  </table>
);

export default AgesTable;
